import React, { Component } from 'react';
import { Row, Col, Button, message, Select, Typography } from 'antd';
import mapboxgl from 'mapbox-gl';
import echarts from 'echarts';
import 'echarts-gl';

import './EchartsMapBoxVis.css'
import { eqArr } from './apis';
import Title from 'antd/lib/skeleton/Title';
const { Option } = Select;
// const { Title } = Typography;

class EchartsMapBoxVis extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // titleText: this.props.chartsParam.titleText ? this.props.chartsParam.titleText : ' ',
      titleText: this.props.titleText,
      mapParam: this.props.chartsParam.mapParam ? this.props.chartsParam.mapParam : null,
    }
    this.myChartGl = null // echarts
    this.mapbox = null // mapbox
    window.mapboxgl = mapboxgl;
    // setTimeout(() => {
    //   this.showmapbox(this.props.data.data, this.props.data.datatime);
    // })
    this.asyncParam = {
      center: [116.368608, 39.901744],
      // Mapbox 
      zoom: 10,
      // ,060
      pitch: 60,
      // Mapbox 
      bearing: -30,
    }

    this.showmapbox = this.showmapbox.bind(this)
  }

  componentDidMount() {
    // window.mapboxgl = mapboxgl;
    this.showmapbox(this.props.data.data, this.props.data.datatime);
    window.onresize = () => {
      this.myChartGl.resize()
    }
  }
  componentDidUpdate() {
    let data = this.props.data.data
    let datatime = this.props.data.datatime
    let asyncParam = this.props.asyncParam ? this.props.chartsParam.mapParam : null

    let newOption = {
      title: {
        text: this.props.titleText,
        subtext: datatime ? datatime : '',
      },
      series: [{
        data: data ? data : [],
        barSize: (2 ** (this.mapbox.getZoom() - 10) * 0.1),
      }],
    }
    if (asyncParam) {
      newOption.mapbox3D = {
        center: asyncParam.center,
        zoom: asyncParam.zoom,
        pitch: asyncParam.pitch,
        bearing: asyncParam.bearing,
      }
    }
    this.myChartGl.setOption(newOption)

    let flyActionParam = this.props.flyActionParam
    if (flyActionParam) {
      this.move_fly(this.mapbox, ...flyActionParam);
    }
  }

  componentWillUnmount() {
    console.log('EchartsMapBoxVis Destory')
    this.myChartGl = null // echarts
    this.mapbox = null // mapbox
    // window.mapboxgl = null
  }

  move_fly = (mapbox, lon, lat, zoom, pitch, bearing, duration) => {
    this.nowMapParam = {
      zoom: this.mapbox.getZoom(),
      center: [this.mapbox.getCenter().lng, this.mapbox.getCenter().lat],
      bearing: this.mapbox.getBearing(),
      pitch: this.mapbox.getPitch()
    }
    if (lon !== this.nowMapParam.center[0] || lat !== this.nowMapParam.center[1] || zoom !== this.nowMapParam.zoom || pitch !== this.nowMapParam.pitch || bearing !== this.nowMapParam.bearing) {
      mapbox.easeTo({
        // CameraOptions
        center: [lon, lat],
        zoom: zoom,
        // ,060
        pitch: pitch,
        // Mapbox 
        bearing: bearing,

        // AnimationOptions
        // 
        duration: duration,
        maxDuration: duration,
        //  0~1   0 1
        easing(t) {
          return t;
          // return t*(2-t);
        },
        // If false , no animation will occur.
        animate: true
      });
    }
  };

  showmapbox = (data = [], datatime = "") => {

    mapboxgl.accessToken = 'pk.eyJ1IjoiaHVzdDEyIiwiYSI6ImNrM3BpbDhsYTAzbDgzY3J2OXBzdXFuNDMifQ.bDD9-o_SB4fR0UXzYLy9gg';

    // echarts
    this.myChartGl = echarts.init(document.getElementById(this.props.mapContainerID));

    // let zoomeLevel = 11
    // let barSize = (2 ** (zoomeLevel - 11)) * 0.08
    this.myChartGl.setOption({
      title: {
        // text: '',
        text: this.state.titleText,
        padding: 20,//5
        subtext: datatime ? datatime : '', //"2019-12-13 14:00", //
        subtextStyle: {//
          color: 'black',
          fontStyle: 'normal',//normalitalic(),oblique()
          fontWeight: "bold",//normal()bold()bolder()lighter()100|200|300|400|500...
          fontFamily: "san-serif",//
          fontSize: 16//12px
        },
      },
      visualMap: {
        type: 'piecewise', //
        categories: ['', '', ''],

        // visualMap-continuous
        show: true, // visualMap-continuous  false
        calculable: true, //
        realtime: true, //
        hoverLink: true,
        left: 20,
        bottom: 40,
        dimension: 2, //, data 
        color: ['red', '#eac736', 'green'],
        pieces: [
          { value: 150, label: '', color: '#369674' }, // value150
          { value: 175, label: '', color: '#feb64d' },
          { value: 200, label: '', color: 'red' },
        ],
        min: 100,
        max: 500,
      },
      // mapbox3D: {
      //   // echarts-glmapboxmapboxmapboxapi
      //   // Mapbox  style
      //   style: 'mapbox://styles/mapbox/outdoors-v11',
      //   // Mapbox ,
      //   center: [116.368608, 39.901744],
      //   // Mapbox 
      //   zoom: 10,
      //   // ,060
      //   pitch: 60,
      //   // Mapbox 
      //   bearing: -30,
      // },
      mapbox3D: this.state.mapParam ? this.state.mapParam : {
        // echarts-glmapboxmapboxmapboxapi
        // Mapbox  style
        style: 'mapbox://styles/mapbox/outdoors-v11',
        // Mapbox ,
        center: [116.368608, 39.901744],
        // Mapbox 
        zoom: 10,
        // ,060
        pitch: 60,
        // Mapbox 
        bearing: -30,
      },

      series: [{
        type: 'bar3D',
        coordinateSystem: 'mapbox3D',
        data: data ? data : [],
        shading: 'color',
        minHeight: 100,
        maxHeight: 500,
        barSize: 0.1,
        //  false
        silent: true, //true 
      }],

    });

    // mapbox
    this.mapbox = this.myChartGl.getModel().getComponent('mapbox3D').getMapbox();

    // mapboxzoom
    this.mapbox.setMinZoom(10);
    this.mapbox.setMaxZoom(16);

    // 
    this.mapbox.addControl(new mapboxgl.NavigationControl({
      showZoom: true,
      showCompass: true,
      visualizePitch: true,
    }), 'top-right');

    // 
    this.mapbox.addControl(new mapboxgl.ScaleControl({
      maxWidth: 80,
      unit: 'metric'
    }), 'bottom-right');

    // //  mapboxecharts
    // this.mapbox.addControl(new mapboxgl.FullscreenControl({
    //   container: document.querySelector('mapbox_echartgl') }
    // ));

    // this.mapbox.on('load', function () {
    //   console.log("")
    //   // window.mapboxgl = null;
    // });

    this.mapbox.on('mousemove', () => {
      let zoomLevel = this.mapbox.getZoom()
      let center = [this.mapbox.getCenter().lng, this.mapbox.getCenter().lat]
      let bearing = this.mapbox.getBearing()
      let pitch = this.mapbox.getPitch()

      if (this.props.asyncParam) {
        if (this.asyncParam.center[0] != center[0] || this.asyncParam.center[1] != center[1] || this.asyncParam.zoom != zoomLevel || this.asyncParam.bearing != bearing || this.asyncParam.pitch != pitch) {
          this.asyncParam.center = center
          this.asyncParam.zoom = zoomLevel
          this.asyncParam.bearing = bearing
          this.asyncParam.pitch = pitch
          this.props.asyncParam(this.asyncParam)
        }
      }
    })
  };

  // mapstyle
  mapStyleChange = (value) => {
    let newStyle = 'mapbox://styles/mapbox/' + value + '-v9'
    // console.log(newStyle)
    this.mapbox.setStyle(newStyle);
  }

  render() {
    return (
      <>
        <div
          className = "mapNav"
          style={{
            width: '100px',
            position: 'absolute',
            top: '12px',
            right: '60px',
            zIndex: 1,
          }}
        >
          {/* <Title level={1}>Ant Design 4.0</Title> */}
          <p className="navTitle"></p>
          <Select
            defaultValue="streets"
            style={{
              width: '100px',
            }}
            onChange={this.mapStyleChange}
          >
            <Option value="streets">streets</Option>
            <Option value="light">light</Option>
            <Option value="dark">dark</Option>
            <Option value="outdoors">outdoors </Option>
            <Option value="satellite">satellite</Option>
          </Select>
        </div>

        <div
          id={this.props.mapContainerID}
          className="mapBoxContainer"
          style={{ minHeight: "500px", height: "100%", width: "100%" }}
        />
      </>
    );
  }
}

export default EchartsMapBoxVis;