import {action, observable} from 'mobx';
import {message} from 'antd';
import request from '../common/service'
import qs from 'qs';
import {processJsonData} from '../common/apis'

class OdStore {
  // MobX ()
  //  @observable (ES.Next)
  @observable dataLeft = []; // 
  @observable dataRight = []; // 
  @observable loading = false; // loading
  @observable scoreMAE = 0; // 
  @observable scoreRMSE = 0; // 

  // 
  // jsonhistorygtpred
  // async & await
  @action async loaddata(jsonPath, type) {
    // 
    let data = {
      data: [],
      datatime: ''
    }
    //
    let response = await fetch(jsonPath)
    let resData = await response.json();
    resData = resData.data
    //
    if (resData === undefined) {
      message.warning('');
    }
    //
    else {
      resData = processJsonData(resData)
      data.data = resData
      data.datatime = jsonPath.split('/')[jsonPath.split('/').length - 1].split('.')[0]
    }

    if(type === 'gt'){
      this.dataLeft = data
    }
    else if(type === 'pred'){
      this.dataRight = data
    }
    else{
      message.warning('loaddata param error');
    }
  }

  // OD
  @action async getOdGt(param){
    this.loading = true;
    //...
    const res = await request.get('data/od/history/' + '?' + qs.stringify(param))
    this.loading = false;
    //...
    if(res === undefined){
      message.error(' undefined')
    }
    //...
    else{
      let data = {
        data: [],
        datatime: ''
      }
      let resData = res.data
      // resData = processJsonData(resData)
      data.data = resData
      data.datatime = res.jsonName
      this.dataLeft = data
      console.log(res)
    }
  }

  // OD
  @action async getOdPred(param){
    this.loading = true;
    // qs.stringify()URL
    // get()url
    const res = await request.get('data/od/pred' + '?' + qs.stringify(param))
    this.loading = false;
    //...
    if(res === undefined){
      message.error(' undefined')
    }
    //...
    else{
      let data = {
        data: [],
        datatime: ''
      }
      let resData = res.data
      // resData = processJsonData(resData) FIXME: 
      data.data = resData
      data.datatime = res.jsonName
      this.dataRight = data
      this.scoreMAE = res.scoreMAE
      this.scoreRMSE = res.scoreRMSE
      console.log(res)
    }
  }


  // 
  @action async clearAll(){
    this.dataLeft = []; // 
    this.dataRight = []; // 
  }
}


const odStore = new OdStore();
export default odStore;