// 4.0 antd  Icon  @ant-design/icons
import {
  HomeOutlined,
  DesktopOutlined,
  PieChartOutlined,
  FileOutlined,
  TeamOutlined,
  UserOutlined,
  BorderOutlined,
  BlockOutlined,
} from '@ant-design/icons';
import React, { Component } from 'react';
import { Layout, Menu, Breadcrumb } from 'antd';
import { Route, Link, withRouter } from 'react-router-dom';


import './BasicLayout.css';
//pages
import Home from '../pages/home/Home';
import Analysis from '../pages/analysis/Analysis';
import PredictionAnalysis from '../pages/prediction/PredictionAnalysis';
import Odpred from '../pages/od/Odpred';

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

// 
// iconmap
const breadcrumbMap = {
  '/analysis': {
    icon: <BorderOutlined />,
    name: 'Analysis',
  },
  '/prediction': {
    icon: <BlockOutlined />,
    name: 'Prediction',
  },
  '/od': {
    icon: <BlockOutlined />,
    name: 'OdPred',
  },
  '/team': {
    icon: <TeamOutlined />,
    name: 'Team',
  },
  '/team/1': {
    icon: <UserOutlined />,
    name: 'Team1',
  },
};

// route  <Route path="/" exact component={Home} />
const routes = [
  {
    path: "/",
    exact: true,
    component: Home
  },
  {
    path: "/analysis",
    exact: true,
    component: Analysis
  },
  {
    path: "/prediction",
    exact: true,
    component: PredictionAnalysis
  },
  {
    path: "/od",
    exact: true,
    component: Odpred
  },
]

// BasicLayout
class BasicLayout extends Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
    }
  }

  onCollapse = collapsed => {
    this.setState({ collapsed });
  };

  componentDidMount() {
  }

  getBreadcrumbItems() { // 
    const { location } = this.props;
    //console.log(location)
    const pathSnippets = location.pathname.split('/').filter(i => i);
    //console.log(pathSnippets)
    const extraBreadcrumbItems = pathSnippets.map((_, index) => {
      const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;

      return (
        <Breadcrumb.Item key={url}>
          {breadcrumbMap[url].icon}
          <Link to={url}>
            {breadcrumbMap[url].name}
          </Link>
        </Breadcrumb.Item>
      );
    });
    const breadcrumbItems = [
      <Breadcrumb.Item key="home">
        <HomeOutlined />
        <Link to="/">
          Home
        </Link>
      </Breadcrumb.Item>,
    ].concat(extraBreadcrumbItems);

    return breadcrumbItems
  }

  render() {
    const BreadcrumbItems = this.getBreadcrumbItems()
    return (
      //Layout Header Sider Content Footer  Layout 
      //https://ant.design/components/layout-cn/
      <Layout style={{ minHeight: '100vh' }}>
        <Sider
          // collapsible
          // collapsed={this.state.collapsed}
          // onCollapse={this.onCollapse}
          width={150}
          style={{
            overflow: 'auto',
            height: '100vh',
            position: 'fixed',
            left: 0,
          }}
        >
          <div className="logo" />
          {/* //! */}
          <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline"
          >
            {/* <Menu.Item key="1" icon={<HomeOutlined />}>
              <Link to="/"></Link>
            </Menu.Item> */}
            <Menu.Item key="2" icon={<BorderOutlined />}>
              <Link to="/analysis"></Link>
            </Menu.Item>
            <Menu.Item key="3" icon={<BlockOutlined />}>
              <Link to="/prediction"></Link>
            </Menu.Item>
            <Menu.Item key="4" icon={<BlockOutlined />}>
              <Link to="/od"></Link>
            </Menu.Item>
            {/* <Menu.Item key="4" icon={<TeamOutlined />}>
              <Link to="/team"></Link>
            </Menu.Item>
            <SubMenu key="sub2" icon={<TeamOutlined />} title="Team">
              <Menu.Item key="6">
                <Link to="/team/1">Team 1</Link>
              </Menu.Item>
              <Menu.Item key="8">Team 2</Menu.Item>
            </SubMenu> */}
            {/* <Menu.Item key="9" icon={<FileOutlined />} >others</Menu.Item> */}
          </Menu>
        </Sider>
        <Layout className="site-layout" style={{ marginLeft: 150 }}>

          <Header className="site-layout-background header-title" style={{ padding: 0 }} >
            
          </Header>

          <Content style={{ margin: '0 16px', overflow: 'initial' }}>
            <Breadcrumb style={{ margin: '16px 0' }}>
              {BreadcrumbItems}
            </Breadcrumb>
            <div id="site-layout-content" className="site-layout-background site-layout-content">
              {
                //! 
                //map  callback 
                routes.map((route, index) => {
                  console.log(route,index)
                  return <Route key={index} path={route.path} exact={route.exact} component={route.component} />
                })
              }
            </div>
          </Content>

          <Footer style={{ textAlign: 'center' }}>
            Traffic Vis System 2020 Created by STDAL
          </Footer>

        </Layout>
      </Layout>
    );
  }
}

export default withRouter(BasicLayout);
