import {message} from 'antd';
import {action, observable} from 'mobx';
import request from '../common/service'
import {processJsonData} from '../common/apis'


class AnalysisStore {
  @observable dataGt = []; // 
  @observable loading = false; // loading

  /**
   * actionnamefnnameStringaction
   * fnFunctionactionaction
   * actionfn
   */
  @action('')
  async getRealTimeData(param){
    this.loading = true
    const res = await request.get('data') // get() URL
    this.loading = false
    // 
    let data = {
      data: [],
      datatime: ''
    }
    if(res === undefined){
      message.error(' undefined')
    }
    else{
      let resData = res.data
      resData = processJsonData(resData)
      data.data = resData
      data.datatime = res.jsonName
      this.dataGt = data //! this
      console.log(res)
    }
  }


  @action('')
  async clearAll(){
    this.dataGt = [];
  }
}


const analysisStore = new AnalysisStore();
export default analysisStore;